"use client";

import { useState, useEffect } from "react";

export default function Home() {
  const [question, setQuestion] = useState("");
  const [answer, setAnswer] = useState("");
  const [apiKey, setApiKey] = useState("");
  const [model, setModel] = useState("gpt-3.5-turbo");
  const [loading, setLoading] = useState(false);
  const [copied, setCopied] = useState(false);
  const [showSettings, setShowSettings] = useState(false);

  useEffect(() => {
    const storedKey = localStorage.getItem("OPENAI_API_KEY");
    if (storedKey) setApiKey(storedKey);
  }, []);

  const askQuestion = async () => {
    if (!question.trim()) return;
    setLoading(true);
    setAnswer("");

    try {
      const res = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL || "http://localhost:8000"}/ask`,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            q: question,
            model,
            api_key: apiKey || null,
          }),
        }
      );

      const data = await res.json();
      if (data.answer) {
        setAnswer(data.answer);
      } else if (data.error) {
        setAnswer(data.error);
      }
    } catch (err) {
      setAnswer("‚ö†Ô∏è Failed to reach backend.");
    } finally {
      setLoading(false);
    }

    if (apiKey) {
      localStorage.setItem("OPENAI_API_KEY", apiKey);
    }
  };

  const copyToClipboard = async (text) => {
    await navigator.clipboard.writeText(text);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const handleKeyPress = (e) => {
    if (e.key === "Enter" && (e.metaKey || e.ctrlKey)) {
      askQuestion();
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 dark:from-gray-900 dark:to-gray-800">
      <div className="container mx-auto px-4 py-8 max-w-4xl">
        <div className="flex items-center justify-between mb-8">
          <div>
            <h1 className="text-3xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
              AI Assistant
            </h1>
            <p className="text-slate-600 dark:text-slate-400 mt-1">
              Ask anything, get intelligent answers
            </p>
          </div>
          <button
            onClick={() => setShowSettings(!showSettings)}
            className="p-2 rounded-lg bg-white dark:bg-gray-800 shadow-sm border border-slate-200 dark:border-gray-700 hover:shadow-md"
          >
            ‚öôÔ∏è
          </button>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          <div className="lg:col-span-2 space-y-4">
            <div className="bg-white dark:bg-gray-800 rounded-xl border p-4">
              <div className="flex gap-3">
                <textarea
                  className="flex-1 resize-none border-0 bg-transparent text-slate-900 dark:text-white placeholder-slate-500 focus:outline-none"
                  placeholder="Ask me anything..."
                  value={question}
                  onChange={(e) => setQuestion(e.target.value)}
                  onKeyDown={handleKeyPress}
                  rows={3}
                />
                <button
                  onClick={askQuestion}
                  disabled={loading || !question.trim()}
                  className="self-end p-3 bg-blue-600 hover:bg-blue-700 text-white rounded-lg"
                >
                  {loading ? (
                    <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin" />
                  ) : (
                    "üì§"
                  )}
                </button>
              </div>
            </div>

            {answer && (
              <div className="bg-white dark:bg-gray-800 rounded-xl border p-6">
                <div className="flex justify-between mb-4">
                  <h2 className="font-semibold text-slate-900 dark:text-white">
                    Response
                  </h2>
                  <button
                    onClick={() => copyToClipboard(answer)}
                    className="p-2 text-slate-500 hover:text-slate-700 dark:hover:text-slate-300"
                  >
                    {copied ? "‚úÖ" : "üìã"}
                  </button>
                </div>
                <p className="text-slate-700 dark:text-slate-300 whitespace-pre-wrap">
                  {answer}
                </p>
              </div>
            )}
          </div>

          <div className={`lg:col-span-1 ${showSettings ? 'block' : 'hidden lg:block'}`}>
            <div className="bg-white dark:bg-gray-800 rounded-xl border p-6">
              <h3 className="font-semibold text-slate-900 dark:text-white mb-4">
                Settings
              </h3>
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                    AI Model
                  </label>
                  <select
                    className="w-full p-3 border rounded-lg bg-white dark:bg-gray-700"
                    value={model}
                    onChange={(e) => setModel(e.target.value)}
                  >
                    <option value="gpt-3.5-turbo">GPT-3.5 Turbo</option>
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                    API Key (Optional)
                  </label>
                  <input
                    type="password"
                    className="w-full p-3 border rounded-lg bg-white dark:bg-gray-700"
                    value={apiKey}
                    placeholder="sk-..."
                    onChange={(e) => setApiKey(e.target.value)}
                  />
                  <p className="text-xs text-slate-500 dark:text-slate-400 mt-1">
                    Leave empty to use server default
                  </p>
                </div>
              </div>
              <div className="mt-6 pt-4 border-t">
                <p className="text-xs text-slate-500 dark:text-slate-400">
                  üí° Tip: Press Cmd/Ctrl + Enter to send your message quickly
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
